<template>
  <div class="clogin" @keyup.enter="login">

    <el-form :model="ruleForm"
             :rules="rules"
             label-position="left"
             ref="ruleForm"
             >
      <el-form-item prop="username" label="用户名"  label-width="80px">
        <el-input placeholder="请输入用户名" v-model="ruleForm.username">
          <i slot="prefix" class="el-icon-user"></i>
        </el-input>
      </el-form-item>
      <el-form-item prop="password" label="密码"  label-width="80px">
        <el-input placeholder="请输入密码" v-model="ruleForm.password" show-password>
          <i slot="prefix" class="el-icon-lock"></i>
        </el-input>
      </el-form-item>
      <el-form-item prop="rememberMe" style="text-align: center" >
        <el-checkbox v-model="ruleForm.rememberMe">3天内免登录</el-checkbox>
          <h4><a href="#" @click.prevent="$message.error('忘记密码无法自行找回，请联系房东修改密码!')">忘记密码？</a></h4>
      </el-form-item>
      <el-form-item class="last">
        <el-button
            type="primary"
            style="width:100%"
            @click="login"
            >登录
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "CLogin",
  props:['username'],
  data(){
    return {
      labelPosition: 'right',
      ruleForm:{
        username:this.username,
        password:'',
        rememberMe:false
      },
      rules:{
        username:[
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {min:4,max:20,message: '用户名长度不少于4',trigger: 'blur'}
        ],
        password:[
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min:6,message: '密码至少为6位',trigger: 'blur'}
        ]
      }
    }
  },
  methods:{
    login(){
      this.$refs.ruleForm.validate((valid) => {
        if(valid){
          var params = new URLSearchParams();
          params.append("username",this.ruleForm.username)
          params.append("password",this.ruleForm.password)
          params.append("rememberMe",this.ruleForm.rememberMe)
          this.$axios.post(this.publicLocation+"/login",params).then(
              response=>{
                if ("username" in response.data) {
                  this.$message.success('登录成功')
                  //  跳转至后台页面
                  this.$router.push({
                        path:'/manage'
                      }
                  )
                }else if (response.data.state==='0'){
                  this.$message.error("用户名不存在或密码错误！")
                }else if (response.data.state==='1'){
                  this.$message.error("抱歉！您的账号被锁定，请联系房东处理！")
                }else if (response.data.state==='2'){
                  this.$message.error("抱歉！您的账号被指定为不可用，请联系房东处理！")
                }
              }
          )
        }else{
          this.$message({
            showClose: true,
            message:'用户名与密码格式不正确',
            type:'error'
          })
        }
      })
    }
  },
  computed:{
    publicLocation(){
      return this.$store.state.publicLocation;
    }
  }
}
</script>

<style scoped>
.clogin {
  /*background-color: red;*/
}
.el-form>.el-form-item {
  margin: 40px 0;
}
.el-form>.el-form-item.last {
  margin: 0!important;
}
.el-button {
  transition: all 1s;
}
.el-button:hover {
  box-shadow: 2px 2px 5px 1px  rgba(0,0,0,0.3);
}

</style>