<template>
  <div>

    <div style="text-align:center;"><h3>查询租客</h3></div>
    <header style="margin: 10px 0">
      <el-row>
        <el-col :span="6">
          <div class="grid-content bg-purple">&nbsp;</div>
        </el-col>
        <el-col :span="6">

        </el-col>
        <el-col :span="6">
          <el-input v-model="search" placeholder="请输入用户名"></el-input>
        </el-col>
        <el-col :span="6" style="text-align: center">
          <el-button type="success" round>点击搜索</el-button>
        </el-col>
      </el-row>
    </header>
    <el-table
        :data="filterData"
        style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item style="color: black;margin-left: 10%">
              <h3>详细个人信息</h3>
            </el-form-item>
            <el-form-item label="用户 ID">
              <span>{{ props.row.userId }}</span>
            </el-form-item>
            <el-form-item label="角色 ID">
              <span>{{ props.row.roleId }}</span>
            </el-form-item>
            <el-form-item label="角色描述">
              <span>{{ props.row.roleDescription }}</span>
            </el-form-item>
            <el-form-item label="用户名">
              <span>{{ props.row.username }}</span>
            </el-form-item>
            <el-form-item label="性别">
              <span>{{ props.row.sex==="2"?"未知":props.row.sex==="1"?"男":"女" }}</span>
            </el-form-item>
            <el-form-item label="姓名">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="电话号码">
              <span>{{ props.row.phoneNumber }}</span>
            </el-form-item>
            <el-form-item label="身份证号码">
              <span>{{ props.row.identity }}</span>
            </el-form-item>
            <el-form-item label="锁定状态">
              <span>{{ props.row.locked ? '已锁定' : '未锁定' }}</span>
            </el-form-item>
            <el-form-item label="可用状态">
              <span>{{ props.row.enable ? '账户可用' : '账户不可用' }}</span>
            </el-form-item>
            <el-form-item label="是否租房">
              <span>{{ props.row.rentState ? '已租' : '未租' }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="用户名"
                       prop="username">
      </el-table-column>
      <el-table-column label="姓名"
                       prop="name"
      >
      </el-table-column>
      <el-table-column label="电话号码"

                       prop="phoneNumber">
      </el-table-column>
      <el-table-column label="是否租房"
                       prop="houseState.rentState">
        <template slot-scope="props">
        <span>
          {{ props.row.rentState ? '已租' : '未租' }}
        </span>
        </template>
      </el-table-column>
      <el-table-column
          prop="address"
          label="操作">
        <template slot-scope="props">
          <div>
            <el-button
                type="primary" round
                style="width: 45%;"
                @click="$router.push({
                path:'/manage/updateRenter',
                query:{
                  isHasData:true,
                  tableData:JSON.stringify(filterData[props.$index])
                }
                })"
            >修改
            </el-button>
            <el-button
                type="danger" round
                style="width: 45%;"
                @click="deleteRenter(props.row.userId)"
            >删除
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "QueryRenter",
  data() {
    return {
      search: '',
      tableData: []
    }
  },
  computed: {
    filterData() {
      return this.tableData.filter((item) => {
        return item.username.indexOf(this.search) !== -1
      })
    }
  },
  methods: {
    deleteRenter(userId) {
      let i;
      for (i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].userId === userId) {
          break;
        }
      }
      this.$confirm(`此操作将删除用户名为【${this.tableData[i].username}】，姓名为【${this.tableData[i].name}】信息，包含租用信息, 且无法撤销！`,
          '危险操作', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'error'
          }).then(() => {
            this.$axios.get(this.$store.state.publicLocation+"/manage/deleteRenter",{
              params:{
                userId:userId
            }
            }).then(
                response=>{
                  if (response.data) {
                    this.$message({
                      type: 'success',
                      message: '删除成功!'
                    });
                    this.tableData.splice(i, 1)
                  }else {
                    this.$message({
                      type: 'error',
                      message: '删除失败！'
                    });
                  }
                }
            )

      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    }
  },
  created() {
    this.$axios.get(this.$store.state.publicLocation + "/manage/listRenterItemVol").then(
        response => {
          console.log(response.data)
          response.data.forEach(data=>{
            let temp = {
              userId: data.uUserVol.userId,
              roleId: data.uUserVol.roleId,
              roleDescription:data.uRole.description,
              username: data.uUserVol.username,
              password: "",
              sex: data.uUserVol.sex,
              name: data.uUserVol.name,
              phoneNumber: data.uUserVol.phoneNumber,
              identity: data.uUserVol.identity,
              locked: data.uUserVol.locked === "1",
              enable: data.uUserVol.enable === "1",
              createdTime: "2021-11-20 13:19:00",
              updateTime: data.uUserVol.updateTime,
              rentState: data.rentState
            }
            this.tableData.push(temp)
          })


        }
    )
  }
}
</script>

<style scoped>

</style>