<template>
  <div class="CMain" :style="cssObj">
    <el-container>
      <el-aside v-show="!isMobile" width="35%"></el-aside>
      <el-main :style="mainTop">
        <div class="hello" v-show="isMobile">
          <h1>Welcome to you!</h1>
        </div>
        <el-row>
          <el-col :span="12">
            <router-link to="/login" active-class="active" checked>登录
            </router-link>
          </el-col>
          <el-col :span="12">
            <router-link to="/register" active-class="active">注册
            </router-link>
          </el-col>
        </el-row>
        <router-view></router-view>
      </el-main>
      <el-aside v-show="!isMobile" width="35%"></el-aside>
    </el-container>
    <div id="icp" >
      <a style="color: blue" href="http://beian.miit.gov.cn/" target=_blank>粤ICP备2021156907号</a>
    </div>
  </div>
</template>

<script>
let range = 50;
let length = 0;
let flag = 1;
let angle = 0;
export default {
  name: "CMain",
  data() {
    return {
      mainTop:{
        margin: '50px 0 250px 0'
      },
      cssObj: {
        backgroundColor: '#FFDEE9',
        backgroundImage: ''
      }
    }
  },
  created() {
    //如果是手机端，登录/注册盒子的外边距下调50px
    if (this.isMobile) this.mainTop.margin='100px 0 250px 0'
  },
  mounted() {
    this.$router.push({path: '/login'});
    setInterval(() => {
      angle = (angle + 1) % 361;
      if (flag === 1) {
        if (++length === range) flag = -1;
      } else {
        if (--length === 0) flag = 1;
      }
      this.cssObj.backgroundImage = `linear-gradient(${angle}deg, #FFDEE9 ${length}%,  #B5FFFC ${length + 50}%, #aae9a6 ${length + 100}%)`;
    }, 60);
  },
  computed:{
    isMobile(){
      return this.$store.state.isMobile
    }
  }
}
</script>

<style scoped>
.CMain {
  /*position: fixed;*/
  display: inline-block;
  width: 100%;
  z-index: -1;
  background-attachment: fixed;
}
.el-main {
  position: relative;
  background-color: white;
  border-radius: 20px;
  transition: all 1s;
  overflow:  visible!important;
  /*height: 800px;*/
}
.el-main:hover {
  box-shadow: 1px 3px 4px 3px rgba(0, 0, 0, 0.3);
  transform: translateY(-5px);
}
.hello {
  position: absolute;
  top: -65px;
  left: 50%;
  margin-left: -116.5px;
  font-size: 25px;
  z-index: 1;
}
.el-col {
  text-align: center;
}

.el-col > a {
  font-size: 18px;
}

.active {
  color: dodgerblue;
  font-weight: 700;
}
#icp{
  text-align: center;
}


</style>