import VueRouter from "vue-router";
import CIndex from "../components/pages/customer/CIndex";
import CLogin from "../components/pages/customer/CLogin";
import CRegister from "../components/pages/customer/CRegister";
import MIndex from "../components/pages/manage/MIndex";
import InsertRenter from "../components/pages/manage/content/renterManage/InsertRenter";
import QueryRenter from "../components/pages/manage/content/renterManage/QueryRenter";
import UpdateRenter from "../components/pages/manage/content/renterManage/UpdateRenter";
import QueryHouseInfo from "../components/pages/manage/content/houseManage/QueryHouseInfo";
import UpdateHouseInfo from "../components/pages/manage/content/houseManage/UpdateHouseInfo";
import InsertHouse from "../components/pages/manage/content/houseManage/InsertHouse";
import BuildingManage from "@/components/pages/manage/content/houseManage/BuildingManage";
const router = new VueRouter({
    routes:[
        {
            path:'/',
            component:CIndex,
            children:[
                {
                    path:'login',
                    component:CLogin,
                    props({query}){
                        return {
                            username:query.username
                        }
                    }
                },{
                    path:'register',
                    component:CRegister
                },
            ]
        },
        {
            path: '/manage',
            component: MIndex,
            children: [
                //renterInfoManage 租客信息管理
                {
                    path: 'insertRenter',
                    component: InsertRenter
                },
                {
                    path: 'queryRenter',
                    component: QueryRenter
                },
                {
                    path: 'updateRenter',
                    component: UpdateRenter
                },
                //houseManage 房屋管理
                {
                    path: 'buildingManage',
                    component: BuildingManage
                },
                {
                    path: 'insertHouse',
                    component: InsertHouse
                },
                {
                    path: 'updateHouseInfo',
                    component: UpdateHouseInfo
                },
                {
                    path: 'queryHouseInfo',
                    component: QueryHouseInfo
                },

            ]

        }
    ]
})
export default router