import Vue from 'vue'
import App from './App.vue'

//vue-cookie
import VueCookies from 'vue-cookies'

//axiox异步通信
import axios from "axios";
//路由插件
import VueRouter from "vue-router";
import router from "./route";
//Vuex
import store from "./store";
//使用饿了么UI
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

//除去生产提示
Vue.config.productionTip = false
//注册
Vue.use(VueRouter)
Vue.use(ElementUI)
Vue.use(VueCookies)
Vue.prototype.$axios = axios

new Vue({
  render: h => h(App),
  router,
  store,
  //  是不是移动端？isMobile赋值
  created() {
      store.state.isMobile = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
    let pathName = window.document.location.pathname;
    store.state.publicLocation = pathName.substring(0, pathName.substr(1).indexOf('/') + 1)
  }
}).$mount('#app')
