<template>
  <div>
    <el-container>
      <el-header height="90px">
        <CHeader/>
      </el-header>
      <el-main>
        <CMain/>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import CHeader from "./CHeader";
import CMain from "./CMain";

export default {
  name: "Customer",
  components: {CHeader, CMain},
  data() {
    return {

    }
  },
  created() {
    let username = this.$cookies.get("username")
    if (username!==null&&username!==""){
      this.$router.push({
        path:'/manage',
        query:{username:username}
      })
    }

  },
}
</script>

