<template>
  <div>
    <div style="text-align:center;"><h3>楼房管理</h3></div>
    <el-table
        :data="tableData"
        stripe
        border
        style="width: 100%;font-size: 16px;">
      <el-table-column
          prop="name"
          label="楼房名"
          width="180"
          sortable>
        <template slot-scope="scope">
        <span v-if="tableData[scope.$index].editing">
          <el-input placeholder="楼房名字" v-model="scope.row.name"></el-input>
        </span>
        <span v-else style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
          prop="address"
          label="地址"
          width="300">
        <template slot-scope="scope">
        <span v-if="tableData[scope.$index].editing">
          <el-input placeholder="楼房地址" v-model="scope.row.address"></el-input>
        </span>
          <span v-else style="margin-left: 10px">{{ scope.row.address }}</span>
        </template>
      </el-table-column>
      <el-table-column
          prop="createdTime"
          label="创建时间"
          >
        <template slot-scope="scope">

          <span v-if="tableData.length-1===scope.$index&&tableData[scope.$index].editing">
            <span style="margin-left: 10px">{{ scope.row.createdTime }}</span>
          </span>
          <span v-if="tableData.length-1!==scope.$index">
            <span style="margin-left: 10px">{{ scope.row.createdTime }}</span>
          </span>
        </template>
      </el-table-column>
      <el-table-column
          prop="address"
          label="操作">
        <template slot-scope="scope">
          <div  v-if="tableData.length-1!==scope.$index">
            <el-button
                v-show="!tableData[scope.$index].editing"
                type="primary" round @click="tableData[scope.$index].editing=true"
                style="width: 45%;"
            >修改</el-button>
            <el-button
                v-show="!tableData[scope.$index].editing"
                type="danger" round @click="deleteInfo(scope.$index)"
                style="width: 45%;"
            >删除</el-button>
            <el-button
                v-show="tableData[scope.$index].editing"
                type="success" round @click="changeInfo(scope.$index)"
                style="width: 100%;margin: 0"
            >确认变更</el-button>
          </div>
          <div v-else>
            <el-button
                v-if="!tableData[scope.$index].editing"
                type="success" round
                style="width: 100%"
                @click="tableData[scope.$index].editing=true;"
            >添加</el-button>
            <el-button
                v-if="tableData[scope.$index].editing"
                type="info" plain
                style="width: 100%"
                @click="addBuilding(scope.$index)"
            >确认添加</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "BuildingManage",
  components:{},
  data() {
    return {
      tableData: []
    }
  },
  methods:{
    deleteInfo(tableDataIndex){
      this.$confirm(`此操作将删除楼房【${this.tableData[tableDataIndex].name}】所有信息，包括其下的所有房屋信息,水电信息，租用信息, 是否继续?`,
          '危险操作', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
        this.tableData.splice(tableDataIndex,1)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    changeInfo(tableDataIndex){
      this.$message.success("修改成功！")
      this.tableData[tableDataIndex].editing=false;
      this.axios.get("url",{
        params:{
          buildingId:this.tableData[tableDataIndex].buildingID
        }
      }).then();

    },
    addBuilding(tableDataIndex){
      let temp = this.tableData[tableDataIndex];
      if (temp.name===""||
          temp.address===""){
        this.$message.error("数据不合法！！")
        return ;
      }else {
        this.$axios.get(this.$store.state.publicLocation+"/manage/addBuilding",{
          params:{
            name:temp.name,
            address:temp.address
          }
        }).then(response=>{
          if (response.data) {
            temp.editing=false;
            this.$message.success("添加楼房成功")
            this.addEmptyItem();
          }
        })
      }

    },
    addEmptyItem(){
      const temp = {
        buildingID: "",
        name: '',
        address: "",
        createdTime: "后台自动生成",
        editing: false
      };
      this.tableData.push(temp);
    }
  },
  created() {
    this.$axios.get(this.$store.state.publicLocation+"/manage/listBuilding").then(response=>{
      response.data.forEach(item=>{
        let temp={
          buildingID: "",
          name: '',
          address: "",
          createdTime: "",
          editing: false
        }
        temp.buildingID=item.buildingId
        temp.name=item.name
        temp.address=item.address
        temp.createdTime=item.createdTime.slice(0,19).replace('T'," ")
        this.tableData.unshift(temp)
      })
    })
    this.addEmptyItem()
  },
  mounted() {
    // alert(this.tableData.length)
  }
}
</script>

