<template>
  <div>
    <div style="text-align:center;"><h3>查询房屋</h3></div>
    <header style="margin: 10px 0">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">&nbsp;</div>
        </el-col>
        <el-col :span="5">
          <el-select v-model="choose" clearable placeholder="选择楼房名">
            <el-option
                v-for="item in searchBuilding"
                :key="item.buildingID"
                :label="item.name"
                :value="item.buildingID">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-input v-model="search" placeholder="房屋门牌号"></el-input>
        </el-col>
        <el-col :span="5" style="text-align: center">
          <el-button type="success" round>点击搜索</el-button>
        </el-col>
        <el-col :span="4">
          <div class="grid-content bg-purple">&nbsp;</div>
        </el-col>
      </el-row>
    </header>
    <el-table
        :data="filterData"
        style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item style="color: black;margin-left: 10%">
              <h3>楼房信息</h3>
            </el-form-item>
            <el-form-item label="楼房 ID">
              <span>{{ props.row.buildingInfo.buildingID }}</span>
            </el-form-item>
            <el-form-item label="楼房名称">
              <span>{{ props.row.buildingInfo.name }}</span>
            </el-form-item>
            <el-form-item label="楼房地址">
              <span>{{ props.row.buildingInfo.address }}</span>
            </el-form-item>
            <el-form-item style="color: black;margin-left: 10%">
              <h3>房屋信息</h3>
            </el-form-item>
            <el-form-item label="房屋 ID">
              <span>{{ props.row.houseInfo.roomID }}</span>
            </el-form-item>
            <el-form-item label="门牌号">
              <span>{{ props.row.houseInfo.houseNumber }}</span>
            </el-form-item>
            <el-form-item style="color: black;margin-left: 10%">
              <h3>房屋状态</h3>
            </el-form-item>
            <el-form-item label="租用状态">
              <span>{{ props.row.houseState.rentState ? '已租' : '未租' }}</span>
            </el-form-item>
            <div v-show="props.row.houseState.rentState">
              <el-form-item label="租用人">
                <span>{{ props.row.houseState.renterName }}</span>
              </el-form-item>
              <el-form-item label="租用时间">
                <span>{{ props.row.houseState.createdTime }}</span>
              </el-form-item>
            </div>

          </el-form>
        </template>
      </el-table-column>
      <el-table-column
          label="附属楼房名"
          prop="buildingInfo.name">
      </el-table-column>
      <el-table-column
          label="房屋门牌号"
          prop="houseInfo.houseNumber"
      >
      </el-table-column>
      <el-table-column
          label="租用状态"
          prop="houseState.rentState">
        <template slot-scope="props">
        <span>
          {{ props.row.houseState.rentState ? '已租' : '未租' }}
        </span>
        </template>
      </el-table-column>
      <el-table-column
          prop="address"
          label="操作">
        <template slot-scope="props">
          <div>
            <el-button
                type="primary" round
                style="width: 45%;"
                @click="$router.push({
                path:'/manage/updateHouseInfo',
                query:{
                  tableData:JSON.stringify(filterData[props.$index])
                }
                })"
            >修改
            </el-button>
            <el-button
                type="danger" round
                style="width: 45%;"
                @click="deleteHouse(props.row.houseInfo.roomID)"
            >删除
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>

</template>


<script>
export default {
  data() {
    return {
      tableData: [],
      choose: "",
      search: ""
    }
  },
  computed: {
    filterData() {

      if (this.choose && this.search === "") return this.tableData.filter((item) => {
        return item.buildingInfo.buildingID === this.choose;
      })
      if (this.choose && this.search) return this.tableData.filter((item) => {
        return item.buildingInfo.buildingID === this.choose && item.houseInfo.houseNumber.indexOf(this.search) !== -1
      })
      if (!this.choose && this.search) return this.tableData.filter((item) => {
        return item.houseInfo.houseNumber.indexOf(this.search) !== -1
      })
      return this.tableData

    },
    searchBuilding() {
      let option = []
      this.tableData.forEach(item => {
        let buildingID = item.buildingInfo.buildingID
        let fall = true;
        option.forEach(item2 => {
          if (buildingID === item2.buildingID) {
            fall = false;
          }
        })
        if (fall) {
          let temp = {
            buildingID: buildingID,
            name: item.buildingInfo.name
          }
          option.push(temp)
        }
      })
      return option;
    }
  },
  methods: {
    deleteHouse(roomId) {
      let i;
      for (i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].houseInfo.roomID === roomId) {
          // this.tableData.splice(i,1);
          break;
        }
      }
      this.$confirm(`此操作将删除【${this.tableData[i].buildingInfo.name}】门牌号为【${this.tableData[i].houseInfo.houseNumber}】的房屋信息，包含租用信息, 且无法撤销！`,
          '危险操作', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'error'
          }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
        this.tableData.splice(i, 1)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });


    }
  },
  created() {
    this.$axios.get(this.$store.state.publicLocation + "/manage/listRoomItemVol").then(
        response => {
          response.data.forEach(item => {
            let temp = {
              buildingInfo: {
                buildingID: item.archiBBuilding.buildingId,
                name: item.archiBBuilding.name,
                address: item.archiBBuilding.address
              },
              houseInfo: {
                roomID: item.archiRRoom.roomId,
                houseNumber: item.archiRRoom.houseNumber,
              },
              houseState: {
                rentState: item.renting.rentState,
                renterName: null,
                createdTime: null
              }
            }
            if (temp.houseState.rentState) {
              temp.houseState.renterName = item.renting.userRealName
              temp.houseState.createdTime = item.renting.createdTime.slice(0, 19).replace('T', " ")

            }
            this.tableData.push(temp)


          })
        }
    )
  }
}
</script>

<style>
.demo-table-expand {
  font-size: 0;
  padding: 0 20px;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>