<template>
  <div>
    <div style="text-align:center;"><h3>修改房屋信息</h3></div>
    <div v-if="fall">
      <el-form  ref="form" :model="tableData" label-width="100px">
        <el-form-item label="附属楼房名">
          <span style="font-size: 16px;margin-left: 35px">{{tableData.buildingInfo.name}}</span>
        </el-form-item>
        <el-form-item label="门牌号" disabled>
          <span style="font-size: 16px;margin-left: 35px">{{tableData.houseInfo.houseNumber}}</span>

        </el-form-item>
        <el-form-item label="租用状态">
          <el-tooltip class="item" effect="dark" content="通过修改租用或停租时间，从而改变租用状态" placement="right">
            <el-switch
                disabled
                v-model="tableData.houseState.rentState"
                active-text="已租"
                inactive-text="空置">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="租用人">
          <el-input v-model="tableData.houseState.renterName" style="width: 24%;"></el-input>
        </el-form-item>
        <el-form-item label="租用时间">
          <el-date-picker
              v-model="tableData.houseState.createdTime"
              type="datetime"
              placeholder="选择日期时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="停租时间">
          <el-date-picker
              v-model="tableData.houseState.stopTime"
              type="datetime"
              placeholder="选择日期时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">确认修改</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </div>
      <div v-else style="text-align: center;color: red;font-size: 30px;padding: 50px 0">
        <h1 >错误！！请先在左边查询房屋信息！</h1>
      </div>

  </div>
</template>

<script>
export default {
  name: "UpdateHouseInfo",
  data() {
    return {
      fall:false,
      tableData:{},
      choose: "",
      options: [
        {
          buildingID: 101,
          name: "A号楼",
        }, {
          buildingID: 102,
          name: "B号楼",
        }, {
          buildingID: 103,
          name: "C号楼",
        },
      ]
    }
  },
  methods:{
    onSubmit(){
      this.$message.success("修改成功")
    }
  },
  created() {
    if (this.$route.query.tableData!==undefined) {
      this.tableData = JSON.parse(this.$route.query.tableData)
      this.fall=true;
    }
  }
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
  padding: 0 50px;
}

</style>