<template>
  <div class="register">
    <el-form :model="registerForm"
             label-position="left"
             :rules="rules"
             ref="registerForm"
             style="margin-top: 20px">
      <el-form-item prop="username" label="用户名" label-width="80px">
        <el-input placeholder="请输入用户名（账号）" v-model="registerForm.username">
          <i slot="prefix" class="el-icon-user"></i>
        </el-input>
      </el-form-item>

      <el-form-item prop="phone" label="手机号" label-width="80px">
        <el-input placeholder="请输入手机号" v-model="registerForm.phone">
          <i slot="prefix" class="el-icon-phone-outline"></i>
        </el-input>
      </el-form-item>

      <el-form-item prop="password" label="密码" label-width="80px">
        <el-input placeholder="请输入密码" v-model="registerForm.password" show-password>
          <i slot="prefix" class="el-icon-lock"></i>
        </el-input>
      </el-form-item>

      <el-form-item prop="twoPassword" label="确认密码" label-width="80px">
        <el-input type="password" placeholder="请再次输入密码" v-model="registerForm.twoPassword">
          <i slot="prefix" class="el-icon-lock"></i>
        </el-input>
      </el-form-item>

      <el-form-item prop="code" label="验证码" label-width="80px" style="position:relative;">
        <el-input placeholder="请输入验证码" v-model.trim="registerForm.code" style="width: 65%;">
          <i slot="prefix" class="el-icon-user"></i>
        </el-input>
        <div class="yzm" @click="t=Math.random()">
          <img :src="publicLocation+'/code/getKaptcha?'+t" alt="">
<!--          <img :src="'http://localhost:80/code/getKaptcha?'+t" alt="">-->
        </div>
      </el-form-item>
      <el-form-item class="last" style="margin: 0">
        <el-button
            type="primary"
            style="width:100%"
            @click="register('registerForm')">注册
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "CRegister",
  data() {
    let validateUsername = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'));
      } else if (!(4 <= value.length && value.length <= 20)) {
        callback(new Error('用户名不得少于4个字符,不超过20'));
      } else if (!/^[\u4E00-\u9FA5A-Za-z0-9]+$/.test(value)) {
        callback(new Error('用户名只能中文、字母和数字，不能包含特殊字符'))
      } else {
        callback();
      }
    };
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));

      } else if (!/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z\\W]{6,18}$/.test(value)) {
        callback(new Error('密码必须包含数字和字母，且长度为6~18'))
      } else {
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.registerForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    let checkPhone = (rule, value, callback) => {
      if (value === '') callback(new Error('请输入手机号'))
      else if (!/^1\d{10}$/.test(value)) callback(new Error('请输入正确的手机号'))
      else callback()
    };
    let verifyCode = (rule, value, callback) => {
      if (value === '') callback(new Error('请输入验证码'))
      else {
        this.$axios.get(this.publicLocation+"/code/verifyCode",{
          params:{
            code:this.registerForm.code
          }
        }).then(response=>{
          if (response.data.state) {
            callback()
          }else {
            callback(new Error('验证码错误！'))
            // this.t=Math.random()
          }
        })
      }

    };

    return {
      registerForm: {
        username: '',
        password: '',
        twoPassword: '',
        phone: '',
        code: ''
      },
      rules: {
        username: [
          // {required: true, message: '请输入用户名', trigger: 'blur'},, message: '用户名不得少于4个字符,不超过20'
          {required: true, validator: validateUsername, trigger: 'blur'},
        ],
        password: [
          {required: true, validator: validatePass, trigger: 'blur'}
        ],
        twoPassword: [
          {required: true, validator: validatePass2, trigger: 'blur'}
        ],
        phone: [
          {required: true, validator: checkPhone, trigger: 'blur'}
        ],
        code: [
          {required: true, validator: verifyCode, trigger: 'blur'}
        ]
      },
      t:1
    }
  },
  methods: {
    register(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var params = new URLSearchParams();
          params.append("username", this.registerForm.username)
          params.append("password", this.registerForm.twoPassword)
          params.append("phoneNumber", this.registerForm.phone)
          this.$axios.post(this.publicLocation + "/uUser/register", params).then(
              response => {
                if (response.data.exist === "1") {
                  this.$message.error("用户名已存在！")
                } else {
                  this.$message.success('注册成功')
                  //  跳转至登录页面
                  this.$router.push({
                    path: '/login',
                    query: {
                      username: this.registerForm.username
                    }
                  })
                }
              }
          )
        } else {
          this.$message.error('信息填写有误，请检查')
        }
      })
    }
  },
  computed: {
    publicLocation() {
      return this.$store.state.publicLocation;
    }
  }
}
</script>

<style scoped>
.yzm {
  display: inline-block;
  position: absolute;
  width: 35%;
  height: 40px;
}

.yzm > img {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.el-button {
  transition: all 1s;
}

.el-button:hover {
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.3);
}
</style>