<template>
  <div>
    <div>
      <div style="text-align:center;"><h3>录入房屋</h3></div>
      <el-form ref="form" :model="tableData" label-width="100px">
        <el-form-item label="附属楼房名">
          <el-select v-model="tableData.buildingId" clearable placeholder="选择楼房">
            <el-option
                v-for="item in tableData.options"
                :key="item.buildingId"
                :label="item.name"
                :value="item.buildingId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="门牌号">
          <el-input v-model="tableData.houseNumber" style="width: 21%;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addHouse">提交</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>

    </div>
  </div>
</template>

<script>
export default {
  name: "InsertHouse",
  data() {
    return {
      tableData: {
        buildingId:'',
        houseNumber:"",
        options: [],
      },
      rules:{
        username:[
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {min:4,max:20,message: '用户名长度不少于4',trigger: 'blur'}
        ],
        password:[
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min:6,message: '密码至少为6位',trigger: 'blur'}
        ]
      }

    }
  },
  methods:{
    addHouse(){
      if (this.tableData.buildingId===0||
          this.tableData.houseNumber===""||
          !/^[0-9]+$/.test(this.tableData.houseNumber)
      ){
        this.$message.error("数据格式不正确！")
        return ;
      }
      //门牌号全楼房唯一
      this.$axios.get(this.$store.state.publicLocation+"/manage/addRoom",{
        params:{
          buildingId:this.tableData.buildingId,
          houseNumber:this.tableData.houseNumber
        }
      }).then(
          response=>{
            if (response.data) {
              this.tableData.houseNumber="";
              this.$message.success("添加成功！")
            }else {
              this.$message.error("添加失败！该楼房当中的门牌号已存在！")
            }
          }
      )
      //注意，这里还需要去数据库中查找水电编号，避免冲突！！！！，水电编号全房屋唯一

      // ajax

    }
  },
  created() {

    this.$axios.get(this.$store.state.publicLocation+"/manage/listBuilding").then(
        response=>{
          response.data.forEach(item=>{
            let temp={
              buildingId:item.buildingId,
              name:item.name
            }
            this.tableData.options.push(temp)

          })

        }
    )

  }
}
</script>

